import moment from 'moment';
import { sessionService } from 'redux-react-session';
import { OutlinedInput } from '@material-ui/core';
import InputAdornment from '@material-ui/core/InputAdornment';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { useCallback, useEffect, useRef, useState } from 'react';
import { connect, useDispatch } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import CheckButton from 'react-validation/build/button';
import Form from 'react-validation/build/form';
import processmakerLogoFinal from '../../assets/images/branding/processmakerLogoFinal.png';
import { needHelpIcon } from '../../assets/images/icons/index';
import { store } from '../../reduxStore/store';
import { setOnBoardingComplete } from '../../reduxStore/actions/flowActions';
import { setSessionSettings } from '../../reduxStore/actions/authActions';
import { setOnBoardingModalsVisibility, setOnBoardingVisible, setIsOnboarding } from '../../reduxStore/actions/modalsActions';
import { gettingInvitationByHash, userRegistration } from '../../services/auth.service';
import { controlledUnsubscription, timezones } from '../../shared/utility';
import { SelectWithSearch } from '../common';
import Button from '../common/UI/Button/Button';
import './AcceptInvitation.scss';

const R = require('ramda');

const AcceptInvitation = props => {
	const navigate = useNavigate();
	const dispatch = useDispatch();
	const { hash } = useParams();
	const form = useRef();
	const checkBtn = useRef();
	const [email, setEmail] = useState('');
	const [username, setUsername] = useState('');
	const [invitation_id, setInvitation_id] = useState('');
	const [invitedBy, setInvitedBy] = useState('');

	const [tzone, setTzone] = useState('America/New_York');
	const [password, setPassword] = useState('');

	const [showPassword, setShowPassword] = useState(false);

	const [loading, setLoading] = useState(false);
	const [message, setMessage] = useState('');
	const [showHelp, setShowHelp] = useState(false);
	const [validInvitation, setValidInvitation] = useState(true);

	const onChangeEmail = e => {
		const email = e.target.value;
		setEmail(email);
	};
	const onChangePassword = e => {
		const password = e.target.value;
		setPassword(password);
	};
	const onChangeUsername = e => {
		const user = e.target.value;
		setUsername(user);
	};
	const { onToggleModalVisibility, onSetOnBoardingComplete, authenticated } = props;
	useEffect(() => {
		onSetOnBoardingComplete(false);
		onToggleModalVisibility({});

		if (!(hash?.length > 0)) return;

		if (authenticated) {
			setValidInvitation(false);
			setMessage('You are currently logged in as another user. Please logout this user and accept the invitation again');
			return;
		}
		const getInvite = gettingInvitationByHash(hash).subscribe({
			next: data => {
				setLoading(false);
				setInvitedBy(data?.user?.first_name ?? ' ' + ' ' + data?.user?.last_name ?? ' ');
				setEmail(data?.email);
				setInvitation_id(data?.id ?? '');
			},
			error: error => {
				setInvitedBy('Error');
				setValidInvitation(false);
				setMessage('Not a valid invitation, could have expired or incorrect. Please contact your admin');
				return;
			},
		});

		return () => {
			controlledUnsubscription(getInvite);
		};
	}, [hash]);
	const handleRegister = useCallback(
		e => {
			e.preventDefault();
			e.stopPropagation();
			setLoading(true);
			form.current.validateAll();

			if (checkBtn?.current?.context?._errors?.length === 0) {
				const registrationData = {
					email: email,
					password: password,
					username: username,
					invitation_id: invitation_id,
					timezone: tzone,
				};
				userRegistration(registrationData).subscribe({
					next: async data => {
						setLoading(false);

						const { is_admin, onboarding_status, access_token, expires_in, refresh_token } = data;

						const sessionSettings = {
							expirationDate: moment()
								.add(+expires_in * 1000, 'seconds')
								.format('YYYY-MM-DD hh:mm:ss:ssss'),
							last_access_token: access_token,
							last_expires_in: expires_in,
							last_refresh_token: refresh_token,
						};
						dispatch(setSessionSettings(sessionSettings));
						const actualSesion = store?.getState()?.sessionReducer ?? {};
						await sessionService?.saveSession?.({ ...actualSesion, ...sessionSettings });
						const authenticatedUser = store?.getState()?.authReducer?.authenticatedUser;
						await sessionService?.saveUser?.(authenticatedUser);
						if (onboarding_status === false) {
							if (is_admin === true) {
								dispatch(setOnBoardingVisible(true));
								dispatch(setIsOnboarding(true));
								dispatch(setOnBoardingModalsVisibility({ firstModalVisible: true }));
								setLoading(false);
							} else {
								navigate('/');
							}
						} else {
							dispatch(setOnBoardingVisible(false));
							dispatch(setIsOnboarding(false));
							dispatch(setOnBoardingComplete(true));
							setLoading(false);
							navigate('/');
						}
					},
					error: error => {
						if (error?.data?.errors?.email?.length > 0) {
							setMessage(error?.data?.errors?.email[0]);
						} else if (error?.data?.errors?.username?.length > 0) {
							setMessage(error?.data?.errors?.username[0]);
						} else if (error?.data?.errors?.password?.length > 0) {
							setMessage(error?.data?.errors?.password[0]);
						} else if (error?.data?.errors?.timezone?.length > 0) {
							setMessage(error?.data?.errors?.timezone[0]);
						} else if (error?.data?.errors?.invitation_id?.length > 0) {
							setMessage(error?.data?.errors?.invitation_id[0]);
						} else {
							setMessage('InvitationAccepted');
						}
						setLoading(false);
					},
					complete: () => {},
				});
			} else setLoading(false);
		},
		[email, password, invitation_id, tzone]
	);
	const showHelpCenter = () => {
		props.onSetOnBoardingVisible(false);
		props.onSetOnBoardingComplete(true);
		navigate('/help-center');
	};
	const GlobalCss = withStyles({
		// @global is handled by jss-plugin-global.
		'@global': {
			// You should target [class*="MuiButton-root"] instead if you nest themes.
			'.MuiOutlinedInput-root': {
				'& fieldset': {
					borderColor: 'var(--app-color-gray-lighter)',
				},
				'&:hover fieldset': {
					borderColor: 'var(--app-color-gray-lighter)!important',
				},
				'&.Mui-focused fieldset': {
					borderColor: 'var(--app-color-gray-lighter)!important',
				},
			},
		},
	})(() => null);
	return (
		<div
			className="main-container"
			style={{
				position: 'relative',
				backgroundColor: 'white',
				display: invitedBy === '' ? 'none' : 'flex',
				flex: 1,
				width: '100%',
				height: '100vh',
				justifyContent: 'space-evenly',
				alignItems: 'stretch',
				overflowY: 'auto',
			}}
		>
			<div className={'first-container'}>
				<div
					style={{
						display: 'flex',
						width: '100%',
						flexDirection: 'column',
						justifyContent: 'flex-start',
						alignItems: 'center',
					}}
				>
					<div
						style={{
							alignSelf: 'flex-start',
							marginTop: '15px',
							marginBottom: '10%',
							display: 'flex',
							flexDirection: 'row',
							justifyContent: 'space-evenly',
							alignItems: 'center',
						}}
					></div>
					{validInvitation ? (
						<div
							style={{
								alignSelf: 'flex-start',
								display: 'flex',
								flexDirection: 'column',
								justifyContent: 'space-evenly',
								alignItems: 'center',
							}}
						>
							<div
								style={{
									alignSelf: 'flex-start',
									display: 'flex',
									flex: 0,
									flexDirection: 'column',
									justifyContent: 'space-evenly',
									alignItems: 'center',
									width: '400px',
								}}
							>
								<h1
									style={{
										padding: 0,
										color: '#4A4A4A',
										fontFamily: 'var(--app-font-gt-walsheim-pro)',
										fontSize: '32px',
										fontWeight: 500,
										letterSpacing: 0,
										lineHeight: '40px',
									}}
								>
									{invitedBy === 'Super' ? 'The request to create your company has been approved by FlowHero' : "You've been invited by "}
									{invitedBy !== 'Super' && (
										<span>
											<i>{invitedBy}</i>
											{' to FlowHero'}
										</span>
									)}
								</h1>
							</div>
							{invitedBy === 'Super' && (
								<div
									style={{
										display: 'flex',
										width: '100%',
										flexDirection: 'row',
										justifyContent: 'flex-start',
										alignItems: 'center',
										margin: '30px 0 0 0 ',
									}}
								>
									<p
										style={{
											fontFamily: 'var(--app-font-quicksand-medium)',
											fontSize: '1em',
											lineHeight: '1.5em',
											color: 'var(--app-color-gray-light)',
										}}
									>
										{'Please provide the details below to finalize the company'}
									</p>
								</div>
							)}
							<div
								style={{
									paddingTop: '30px',
									alignSelf: 'flex-start' /*  boxSizing: 'border-box', height: '57px', width: '351px', border: '1px solid #D2D2D2', borderRadius: '8px', backgroundColor: '#FFFFFF' */,
								}}
							>
								<Form ref={form}>
									<p className={'labelTextStyle'}>{'Account Email:'}</p>
									<TextField id="my-input" type="text" variant="outlined" name="name" className="input-style" placeholder={'Company Email'} value={email} onChange={onChangeEmail} />
									{message?.email ? (
										<div className="form-group">
											<div
												style={{
													color: '#721c24',
													backgroundColor: '#f8d7da',
													borderColor: '#f5c6cb',
												}}
											>
												{message?.email}
											</div>
										</div>
									) : null}
									<br />
									<br />
									<TextField id="my-input" type="text" variant="outlined" name="username" className="input-style" placeholder={'Desired Username'} value={username} onChange={onChangeUsername} />
									{message?.username ? (
										<div className="form-group">
											<div
												style={{
													color: '#721c24',
													backgroundColor: '#f8d7da',
													borderColor: '#f5c6cb',
												}}
											>
												{message?.username}
											</div>
										</div>
									) : null}
									<br />
									<br />
									<p className={'labelTextStyle'}>{'Account Password:'}</p>

									<label style={{ display: 'inline-flex', alignItems: 'center' }}>
										<GlobalCss />
										<OutlinedInput
											id="my-input"
											className="input-style"
											type={showPassword ? 'text' : 'password'}
											name="password"
											placeholder="Password"
											value={password}
											onChange={onChangePassword}
											endAdornment={
												<InputAdornment className="showHideButton" onClick={() => setShowPassword(!showPassword)} position="end">
													{showPassword ? 'Hide' : 'Show'}
												</InputAdornment>
											}
										/>
									</label>
									{message?.password ? (
										<div className="form-group">
											<div className="alert alert-danger" role="alert">
												{message?.password}
											</div>
										</div>
									) : null}
									<br />
									<br />
									<p className={'labelTextStyle'}>Timezone:</p>
									<div
										style={{
											display: 'flex',
											flexDirection: 'column',
											justifyContent: 'flex-start',
											alignItems: 'stretch',
											width: '100%',
										}}
									>
										<SelectWithSearch //isClearable
											items={timezones}
											defaultSelectedItem={R.find(R.propEq('value', tzone))(timezones)}
											triggerType={'chevron'}
											placeholder={'Select timezone...'}
											onChange={selection => {
												setTzone(selection?.value);
											}}
										/>
									</div>
									<br />

									<br />
									<div
										style={{
											display: 'flex',
											width: '100%',
											flex: 0,
											justifyContent: 'flex-start',
											alignItems: 'center',
											maxHeight: '30px' /*  height: '56px',width: '116px' */,
										}}
									>
										<Button disabled={loading} loading={loading} clicked={handleRegister} title={invitedBy === 'Super' ? 'Create Company' : 'Accept Invitation'} style={{ margin: '0' }} />
									</div>
									{message && (
										<div
											style={{
												display: 'flex',
												flexDirection: 'column',
												justifyContent: 'space-evenly',
												alignItems: 'center',
												fontSize: '1em',
												width: '90%',
												marginTop: '30px',
												borderRadius: '3px',
												color: '#721c24',
												backgroundColor: '#f8d7da',
												borderColor: '#f5c6cb',
												padding: '2px',
											}}
											role="alert"
										>
											{message === 'InvitationAccepted' ? (
												<div>
													Already accepted the invitation, click <a href={'/'}>here</a> to login.
												</div>
											) : (
												message
											)}
										</div>
									)}
									<CheckButton style={{ display: 'none' }} ref={checkBtn} />
								</Form>
							</div>
							<div
								style={{
									display: 'flex',
									width: '100%',
									flexDirection: 'row',
									justifyContent: 'flex-start',
									alignItems: 'center',
									margin: '30px 0 0 0 ',
								}}
							>
								<p>
									FlowHeroâ„¢, you agree with FlowHero <a href={'https://processmaker.com/terms-conditions'}>Terms and Conditions</a> and <a href={'https://processmaker.com/privacy-policy'}>Privacy Policy</a>
								</p>
							</div>
						</div>
					) : (
						<div
							style={{
								display: 'flex',
								flexDirection: 'column',
								justifyContent: 'space-evenly',
								alignItems: 'center',
								fontSize: '1em',
								width: '90%',
								marginTop: '30px',
								borderRadius: '3px',
								color: '#721c24',
								backgroundColor: '#f8d7da',
								borderColor: '#f5c6cb',
								padding: '2px',
							}}
							role="alert"
						>
							{message}
						</div>
					)}
				</div>
			</div>
			<div
				className={'second-container'}
				onClick={e => {
					e.preventDefault();
					e.stopPropagation();
					setShowHelp(false);
				}}
			></div>
			{showHelp && (
				<div
					style={{
						width: '300px',
						flex: 1,
						flexWrap: 'wrap',
						padding: '12px 30px 0px 10px',
						zIndex: 101,
						position: 'absolute',
						bottom: '0px',
						right: '50px',
						display: 'flex',
						flexDirection: 'column',
						justifyContent: 'space-between',
						alignItems: 'center',
						borderRadius: '8px 8px 0 0',
						backgroundColor: ' #FFFFFF',
						boxShadow: '0 0 40px 0 rgba(0, 0, 0, 0.1)',
					}}
				>
					<div
						style={{
							display: 'flex',
							flex: 1,
							flexDirection: 'row',
							justifyContent: 'space-evenly',
							alignItems: 'center',
							height: '54px',
							width: '149px',
						}}
					>
						<img src={needHelpIcon} style={{ height: '24px', width: '24px' }} alt="need-help-icon" />
						<p
							style={{
								color: ' #4A4A4A',
								fontFamily: ' var( --app-font-quicksand-bold)',
								fontSize: '16px',
								lineHeight: '22px',
							}}
						>
							{'Need help?'}
						</p>
					</div>
					<div
						style={{
							display: ' flex',
							flex: 1,
							flexDirection: 'row',
							justifyContent: 'space-evenly',
							alignItems: 'center',
						}}
					>
						<p
							style={{
								textAlign: 'left',
								color: ' #4A4A4A',
								fontFamily: ' var( --app-font-quicksand-bold)',
								fontSize: '16px',
								lineHeight: '24px',
							}}
						>
							{'Before requesting to speak to one of our colleagues, we encourage you to explore our '}
							<span
								onClick={showHelpCenter}
								style={{
									textDecoration: 'underline',
									cursor: 'pointer',
								}}
							>
								{'Help Center'}
							</span>
							{' to find your solution.'}
						</p>
					</div>
					<div
						style={{
							margin: '20px 10px',
							display: 'flex',
							width: '100%',
							flex: 0,
							justifyContent: 'center',
							alignItems: 'center',
							maxHeight: '30px' /*  height: '56px',width: '116px' */,
						}}
					>
						<Button disabled={loading} loading={loading} clicked={showHelpCenter} title={'Help Center'} style={{ margin: '10px', width: '100%' }} />
					</div>
					<div
						style={{
							margin: '20px 10px',
							display: 'flex',
							width: '100%',
							flex: 0,
							justifyContent: 'center',
							alignItems: 'center',
							maxHeight: '30px' /*  height: '56px',width: '116px' */,
						}}
					>
						<Button
							disabled={loading}
							loading={loading}
							clicked={() => {
								alert('Live Chat');
							}}
							title={'Live Chat'}
							style={{ margin: '10px', width: '100%', backgroundColor: '#FFFFFF' }}
							titleStyle={{ color: 'var(--app-color-gray-light)', fontSize: '16px' }}
						/>
					</div>
				</div>
			)}
		</div>
	);
};

const mapStateToProps = store => {
	const { sessionSettings } = store.authReducer;
	const { onBoardingCompleted } = store.flowReducer;
	return {
		onBoardingCompleted,
		sessionSettings,
	};
};
const mapDispatchToProps = dispatch => {
	return {
		onSetOnBoardingVisible: visible => dispatch(setOnBoardingVisible(visible)),
		onSetOnBoardingComplete: completed => dispatch(setOnBoardingComplete(completed)),
		onToggleModalVisibility: modalVisible => dispatch(setOnBoardingModalsVisibility(modalVisible)),
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(AcceptInvitation);
